.progress__bar {
  position: relative;
  width: 20.5rem;
  height: 1.4rem;
  background-color: #fff;
  border-radius: 1rem;
  box-shadow: inset 0 1.5px 0 0.5px #cecece;
}
.progress__bar::after {
  content: "20%";
  display: block;
  height: 100%;
  font-size: 1.2rem;
  font-weight: 700;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding-top: 0.7rem;
  z-index: 100;
}
.progress__line {
  display: inline-block;
  width: 20%;
  height: 100%;
  background: linear-gradient(0deg, #67FF51, #00CE20, #67FF51);
  z-index: 10;
  border-radius: 1rem;
}

.logo {
  width: 14rem;
  position: relative;
}
.logo__title-box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
}
.logo__char {
  font-size: 5.5rem;
  text-transform: uppercase;
  font-weight: 700;
  color: rgba(41, 38, 38, 0.842);
  transform: rotate(-40deg);
}
.logo__box {
  display: flex;
  gap: 1rem;
  align-items: center;
}
.logo__item, .logo__item-1, .logo__item-2, .logo__item-3 {
  border-radius: 1rem;
  background: linear-gradient(#fff, blue, red);
  border: 0.1rem solid rgb(39, 37, 37);
}
.logo__item {
  width: 1rem;
  height: 4rem;
  background-color: #000;
}
.logo__item-1 {
  width: 1rem;
  height: 5rem;
  background-color: #000;
}
.logo__item-2 {
  width: 1rem;
  height: 6rem;
  background-color: #000;
}
.logo__item-3 {
  width: 1rem;
  height: 7rem;
}

.menu-burger {
  display: none;
  position: absolute;
  width: 100%;
  height: 10rem;
  background-color: #fff;
}
@media (max-width: 450px) {
  .menu-burger {
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 2px 0px 5px 2px rgba(0, 0, 0, 0.2);
    padding: 0 1.5rem;
    z-index: 10;
  }
}
.menu-burger__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 0.5rem solid #7E7E7E;
  background: linear-gradient(to top, #7E7E7E 1%, #999999 2%, #A5A5A5 3%, #ADADAD 4%, #B5B5B5 5%, #BDBDBD 5%, #CBCBCB 80%);
  border-radius: 0.5rem;
  padding: 0 0.3rem;
  max-width: 6rem;
  max-height: 6rem;
  cursor: pointer;
}
.menu-burger__btn:hover {
  border-color: #333333;
}
.menu-burger__btn:hover .menu-burger__icon {
  stroke: #333333;
  transform: rotate(180deg);
}
.menu-burger__btn:hover .menu-burger__icon--close {
  fill: #333333;
}
.menu-burger__icon {
  width: 5rem;
  height: 5rem;
  stroke: #7E7E7E;
  stroke-width: 3;
  stroke-linecap: round;
}
.menu-burger__icon.hidden {
  display: none;
}
.menu-burger__icon--close {
  display: none;
  fill: #7E7E7E;
}
.menu-burger__icon--close.active {
  display: block;
}

