.radio__fake {
  position: absolute;
  width: 0.1rem;
  height: 0.1rem;
  opacity: 0;
}
.radio__fake:checked + .radio__true {
  border-bottom: none;
  background: #8CB903;
  padding: 0.5rem 0.7rem;
  border-radius: 0.5rem;
  color: #fff;
}
