.btn {
  display: inline-block;
  color: #000;
  font-family: inherit;
  line-height: 0;
  font-weight: 700;
  background: linear-gradient(to bottom, #f2f2f2 20%, #ededed 10%, #e2e2e2 70%);
  cursor: pointer;
  border-bottom: 0.1rem solid #a8a8a8;
  border-right: 0.1rem solid #ccc;
  border-left: 0.1rem solid #ccc;
  border-top: 0.1rem solid #e5e5e5;
  margin-bottom: 1rem;
}
.btn:hover {
  color: #148003;
}

.btn--small {
  font-size: 1.6rem;
  padding: 1.5rem 5rem;
  border-radius: 1.5rem;
}

.btn--normal {
  font-size: 1.8rem;
