.button-box {
  width: 100%;
  display: flex;
  justify-content: space-between;
  background: #F2F2F2;
  padding: 2rem 1rem;
}

.button-prev,
.button-next {
  cursor: pointer;
  color: #fff;
  border: none;
  font-size: 1.6rem;
  border-radius: 3rem;
  background: linear-gradient(to top, #7E7E7E 1%, #999999 2%, #A5A5A5 3%, #ADADAD 4%, #B5B5B5 5%, #BDBDBD 5%, #CBCBCB 80%);
}
.button-prev:hover,
.button-next:hover {
  background: #9DD005;
}
.button-prev:focus,
.button-next:focus {
  border: none;
  outline: none;
}

.button-prev {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  padding: 1rem 3rem 1rem 6rem;
}
.button-prev::before {
  content: "";
  display: inline-block;
  width: 3.2rem;
  height: 5rem;
  position: absolute;
  top: -40%;
  left: 16%;
  transform: rotate(180deg);
  background-image: url("../img/svg/next_arrow.svg");
  background-repeat: no-repeat;
}

.button-next {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  padding: 1rem 6rem 1rem 3rem;
}
.button-next::after {
  content: "";
  display: inline-block;
  width: 3.2rem;
  height: 5rem;
  position: absolute;
  top: 10%;
  right: 16%;
  background-image: url("../img/svg/next_arrow.svg");
  background-repeat: no-repeat;
}

